.page-flat {
    font-family: $main-font;
    position: relative;
    z-index: 0;
    &:after {
        content: '';
        position: absolute;
        background-color: $color-primary-light;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: -1;
    }
    & .flat_content {
        display: none;
        &.active {
            display: block;
        }
        & .flat_svg {
            display: flex;
            justify-content: center;
            & .flat_img {
                height: calc(100vh - 230px);
                & .flat_item {
                    fill: $fill-green;
                    fill-opacity: 0.1;
                    stroke: $text-color;
                    stroke-width: 4px;
                    cursor: pointer;
                }
            }
        }
    }
    & .information_container  {
        & .information {
            & .flat_info {
            @extend .information_list; 
                & .list_item {
                    min-width: 152px;
                }
            }
        }
    }
}